@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  margin: 1rem 0.75rem;
  background-color: #fff;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  border-radius: 3px;
}

.card img {
  width: 100%;
}

.card-info {
  color: #1d2228;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  letter-spacing: 0.5px;
}

.card-info h3 {
  margin: 0;
}

.card-info small,
.card h3 {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.overview {
  background-color: #fff;
  padding: 0.75rem 2rem;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  max-height: 100%;
  transform: translateY(101%);
  overflow-y: auto;
  transition: transform 0.3s ease-in;
  color: #1d2228;
  font-size: 1rem;
}

.overview h3 {
  margin: 1rem 0 0;
}

.card:hover .overview {
  transform: translateY(0);
}

.card-group {
  display: flex;
  flex-wrap: wrap;
}

.container h2 {
  margin: 1em 0;
}

.card-group--content {
  width: 20%;
}

@media (max-width: 1024px) {
  .card-group--content {
    width: 33%;
  }
}

@media (max-width: 800px) {
  .card-group--content {
    width: 50%;
  }
}

.pagination {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #e1e2e2;
  width: 250px;
  height: 50px;
  color: #1d2228;
}

.pagination-number {
  text-align: center;
  width: 150px;
  padding: 0 10px;
}

.divider {
  background-color: rgba(29, 34, 40, 0.05);
  width: 1px;
  height: 100%;
  padding: 0;
}

.pagination button {
  background-color: transparent;
  border: none;
  width: 48px;
  height: 48px;
  font-size: 1.25em;
  cursor: pointer;
  padding: 0;
}

.pagination button:hover {
  background-color: rgba(29, 34, 40, 0.05);
}

.pagination button:focus {
  outline: #fb8122 solid 1px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #1d2228;
  font-family: 'IBM Plex Sans', sans-serif;
  margin: 0 auto;
  padding: 1em 2em;
  color: #fff;
  height: 100vh;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app-header h1 {
  margin: 0 0.5em 0 0;
  color: #fb8122;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
}

.app-header--nav {
  flex-wrap: wrap;
}

.search {
  background-color: transparent;
  border: 2px solid #e1e2e2;
  border-radius: 50px;
  font-family: inherit;
  font-size: 1rem;
  padding: 0.5rem 1rem;
}

.search::-webkit-input-placeholder {
  color: #fff;
}

.search:-ms-input-placeholder {
  color: #fff;
}

.search::-ms-input-placeholder {
  color: #fff;
}

.search::placeholder {
  color: #fff;
}

.search:focus {
  outline: none;
  background-color: #e1e2e2;
  color: #1d2228;
}

.app-header--nav {
  display: flex;
  flex: 2 1;
}

.app-header--nav button {
  text-transform: uppercase;
  cursor: pointer;
  padding: 1rem;
  background-color: transparent;
  border: none;
  font-size: 1rem;
  color: #fff;
  height: 40px;

  letter-spacing: 0.05em;
}

.app-header--nav .active {
  color: #fb8122;
  font-weight: 700;
}

.app-header--nav button:hover {
  color: #fb8122;
  text-decoration: line-through;
}

.app-header--nav button:focus {
  outline: none;
}

.active:hover {
  color: #fff;
}

.app-main {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-pagination {
  background-color: #e1e2e2;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

@media (max-width: 800px) {
  .app-header {
    flex-direction: column;
  }

  .search {
    width: 85vw;
  }

  .app-header--nav {
    justify-content: space-around;
  }

  .app-header--nav button {
    padding: 0 1rem 0 0;
  }
}

